[data-tooltip] {
  position: relative;
  /* Относительное позиционирование */
}
[data-tooltip]:hover::after {
  content: attr(data-tooltip);
  /* Выводим текст */
  position: absolute;
  /* Абсолютное позиционирование */
  width: 200px;
  left: 0;
  background: #9f9f9f;
  color: #fff;
  padding: 0.5em;
  pointer-events: none;
  /* Подсказка */
  transition: 1s;
  /* Время появления подсказки */
  opacity: 1;
  /* Показываем подсказку */
  top: 36px;
  /* Положение подсказки */
}
